import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Input, OnInit, TemplateRef, ViewChild, inject } from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { Router } from '@angular/router';
import { IConfigTable, IInventory, IProduct, IProductLanguage, TYPE_FORM_CONTROL, TYPE_TEMPLATE_REF } from '@icom/interfaces';
import { IComLibsServicesInventoryService, IComLibsServicesProductService } from '@icom/services/projects';
import { FormlyFieldConfig, FormlyFormOptions, FormlyModule } from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { combineLatest, lastValueFrom } from 'rxjs';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { NgIf, NgSwitch, NgSwitchCase } from '@angular/common';
import { NzIconModule } from 'ng-zorro-antd/icon';
import { NzModalService } from 'ng-zorro-antd/modal';
import { IComMessageService } from '@icom/services/common';
import { ConfigColumnInventory } from '../defines/product.define';
import { NzDropDownModule } from 'ng-zorro-antd/dropdown';
import { Page } from '../defines/page.model';

@Component({
  selector: 'icom-libs-inventory-detail-product',
  templateUrl: './product.component.html',
  standalone: true,
  imports: [
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NgxDatatableModule,
    NgIf,
    NzIconModule,
    NzDropDownModule,
    NgSwitch,
    NgSwitchCase
  ],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class IcomLibsInventoryDetailProductComponent implements OnInit {
  protected form = new FormGroup({});
  protected options: FormlyFormOptions = {};
  protected modelForm: IProductLanguage;
  protected pagination = new Page();
  protected configTable!: IConfigTable;
  protected loadingIndicator!: boolean;
  protected isAddNew!: boolean;

  @Input() fields: FormlyFieldConfig[] = [];
  @Input() rows: IProductLanguage[] | undefined;
  @Input() inventory: IInventory | undefined;

  protected productService = inject(IComLibsServicesProductService);
  protected modalService = inject(NzModalService);
  protected IComMessageService = inject(IComMessageService);
  protected inventoryService = inject(IComLibsServicesInventoryService)
  protected cdr = inject(ChangeDetectorRef);

  @ViewChild('selectRef', { static: true }) selectRef!: TemplateRef<TYPE_TEMPLATE_REF>;
  @ViewChild('statusRef', { static: true }) statusRef!: TemplateRef<TYPE_TEMPLATE_REF>;
  
  constructor(
    private router: Router,
  ) {
    this.modelForm = {};
    this.pagination.pageNumber = 0;
  }

  ngOnInit() {
    this.configTable = {
      loadingIndicator: true,
      columns: ConfigColumnInventory(this.selectRef, this.statusRef),
      headerHeight: 50,
      rowHeight: 'auto',
      pageLimit: 10
    }
    combineLatest([
      this.productService.getOptionType('PRODUCT_TYPE'),
			this.productService.getOptionType('PRODUCT_GENCODE_TYPE')]
    ).subscribe((results: any) => {
      const [dataType, dataGenCodeType] = results;
      const columnType = this.configTable.columns?.find(item => item.prop === 'type');
      columnType && (columnType.pipe = this.pipeParseStatus(dataType));
      this.configTable.columns = this.configTable.columns;

      const columnGenCodeType = this.configTable.columns?.find(item => item.prop === 'gen_code_type');
      columnGenCodeType && (columnGenCodeType.pipe = this.pipeParseStatus(dataGenCodeType));
      this.configTable.columns = this.configTable.columns;
    });
    this.loadPage();
  }

  private pipeParseStatus(data: any) {
    const listStatus = data.data as any[];
    return {
      transform: (value: string): string  => {
        const statusObj = listStatus.find(item => item.code === value);
        return statusObj.name;
      }
    }
  }
  
  protected async loadPage() {
    try {
      this.loadingIndicator = true;
      const result = await lastValueFrom(this.inventoryService.getListProductOfInventory(this.inventory?.id || ''));

      if (result && result.data && result.data.items) {
        const rows = [...result.data.items || []];
        this.rows = rows;
      }
    } catch (err) {
      console.log(err);
    } finally {
      this.loadingIndicator = false;
      this.cdr.detectChanges();
    }
  }

  protected handlerViewDetail(e: Event, row: IProduct) {
    e.stopPropagation();
    this.router.navigateByUrl(`/inventory/product?inventory=${this.inventory?.id}&product=${row.id}`);
  }
}
