import { Component, Input, OnInit, inject } from "@angular/core";
import { IInventory } from "@icom/interfaces";
import { IComLibsServicesInventoryService } from "@icom/services/projects";
import { IcomLibsInventoryAddEditComponent } from '@icom/inventory';
import * as dayjs from "dayjs";
import { NzSpinModule } from 'ng-zorro-antd/spin';
import { NgIf } from "@angular/common";

@Component({
  selector: 'icom-libs-inventory-detail-general',
  templateUrl: './general.component.html',
  standalone: true,
  imports: [
    IcomLibsInventoryAddEditComponent,
    NzSpinModule,
    NgIf
  ]
})
export class IcomLibsInventoryDetailGeneralComponent implements OnInit {
  
  @Input() inventory: IInventory | undefined;
  
  protected inventoryService = inject(IComLibsServicesInventoryService)
  
  ngOnInit() {
    if(!this.inventory) {
      return;
    }
    if(!this.inventory.rent_from || !this.inventory.rent_to) {
      return;
    }
    this.inventory.date_range = [dayjs(this.inventory.rent_from).toDate(), dayjs(this.inventory.rent_to).toDate()];  
  }

}
