import { TemplateRef } from "@angular/core";
import { TYPE_TEMPLATE_REF } from "@icom/interfaces";
import { formatDatePipe, defaultValuePipe } from "@icom/defines";

export const ConfigColumnInventory = (selectRef: TemplateRef<TYPE_TEMPLATE_REF>, statusRef: TemplateRef<TYPE_TEMPLATE_REF>) => {
  return [
    { prop: '', cellTemplate: selectRef, width: 120,sortable: false },
    { prop: 'sku', sortable: false, width: 200 },
    { prop: 'name', sortable: false, width: 400 },
    { prop: 'type', width: 200, sortable: false  },
    { prop: 'order', width: 100, sortable: false, pipe: defaultValuePipe()  },
    { prop: 'gen_code_type', width: 200, sortable: false, name: 'Gen Code Type'   },
    { prop: 'start_time', width: 150, sortable: false, pipe: formatDatePipe() },
    { prop: 'end_time', width: 150, sortable: false, pipe: formatDatePipe()   },
    { prop: 'status', width: 100, sortable: false, cellTemplate: statusRef }
  ]
};
