import { NgForOf, NgSwitch, NgSwitchCase, NgIf } from '@angular/common';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit, inject } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { IInventory, IProduct } from '@icom/interfaces';
import { IComLibsServicesInventoryService, IComLibsServicesProductService } from '@icom/services/projects';
import { FormlyModule } from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { NzFormModule } from 'ng-zorro-antd/form';
import { NzIconModule } from 'ng-zorro-antd/icon';
import { NzPageHeaderModule } from 'ng-zorro-antd/page-header';
import { NzSpaceModule } from 'ng-zorro-antd/space';
import { NzTabsModule } from 'ng-zorro-antd/tabs';
import { NzTypographyModule } from 'ng-zorro-antd/typography';
import { IcomLibsInventoryDetailGeneralComponent } from './general/general.component';
import { IcomLibsInventoryDetailProductComponent } from './product/product.component';
import { NzSpinModule } from 'ng-zorro-antd/spin';

@Component({
  selector: 'icom-libs-inventory-detail',
  templateUrl: './detail.component.html',
  standalone: true,
  imports: [
    NzTypographyModule,
    NzPageHeaderModule,
    NzSpaceModule,
    NzTabsModule,
    NzIconModule,
    NgIf,
    IcomLibsInventoryDetailGeneralComponent,
    IcomLibsInventoryDetailProductComponent,
    NzSpinModule
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,
})

export class IcomLibsInventoryDetailComponent implements OnInit {
  protected inventory: IInventory | undefined;
	protected inventoryId: string;
  protected inventoryService = inject(IComLibsServicesInventoryService)
  protected cdr = inject(ChangeDetectorRef);
	constructor(private routeActive: ActivatedRoute) {
		this.inventoryId = this.routeActive.snapshot.params['id'];
	}

	ngOnInit(): void {
		this.inventoryService.getInventory(this.inventoryId).subscribe((response: any) => {
			this.inventory = response.data;
      this.cdr.detectChanges();
		});
	}
}
